                                control_width: "50%"
                            },
                        },
                    ]
                }
            ];
        },
        sale_text() {
            return this.data && this.data.settings && 'sale_text' in this.data.settings
                ? this.data.settings.sale_text
                : '';
        },
        sold_text() {
            return this.data && this.data.settings && 'sold_text' in this.data.settings
                ? this.data.settings.sold_text
                : '';
        },
        label_badge_tags() {
            return this.data?.settings?.label_badge_tags ?? "".split("\n").trim().join(",");
        },
        screens() {
            let screens = this.$store.getters["builder/screens"];
            if (!screens) {
                return [];
            }
            return Object.entries(screens).sort(([, b1], [, b2]) => b1.width - b2.width);
        },
        screenMinW() {
            let rs = [];
            for (let i in this.screens) {
                let screen = this.$helpers.copy(this.screens[i]);
                screen[1].width = i === "0" ? 0 : parseInt(this.screens[i - 1][1].width) + 1;
                rs.push(screen);
            }
            return rs;
        },
        breakpoints() {
            let bp = {},
                settings = this.data.settings;
            let ops = {itemsPerView: 'slidesPerView', itemsSpace:'spaceBetween'};
            this.screenMinW.forEach(([, v]) => {
                bp[v.width] = {
                    __screen_name : v.name
                };
                Object.keys(ops).forEach((op) => {
                    let key = v.name === "desktop" ? op : op + "__" + v.name;
                    bp[v.width][ops[op]] = settings[key];
                });
            });
            return bp;
        },
        thumbsBreakpoints() {
            let bp = {},
                settings = this.data.settings;
            let ops = ["slidesPerView", "spaceBetween","thumbnail_position"];
            this.screenMinW.forEach(([, v]) => {
                bp[v.width] = {
                    __screen_name : v.name
                };
                ops.forEach((op) => {
                    let key = v.name === "desktop" ? op : op + "__" + v.name, position_key = v.name === "desktop" ? 'thumbnail_position' : 'thumbnail_position' + "__" + v.name;
                    if(op == 'slidesPerView') {
                        if(['row', 'row-reverse'].includes(settings[position_key])) {
                            bp[v.width][op] = 'auto'
                        } else {
                            bp[v.width][op] = settings[key];
                        }
                    } else {
                        bp[v.width][op] = settings[key];
                    }
                });
            });
            return bp;
        },
        thumbClass() {
            let settings = this.data.settings;
            return {
                'ecom-swiper-tablet-vertical': ['row','row-reverse'].includes(settings?.thumbnail_position__tablet),
                'ecom-swiper-mobile-vertical': ['row','row-reverse'].includes(settings?.thumbnail_position__mobile)
                }
        },
        javascript() {
            return function () {
                if(!this.$el)
                {
                    return false;
                }

                // eslint-disable-next-line @typescript-eslint/no-this-alias
                const _this = this;
                const $el = this.$el,isLive = this.isLive;

                // eslint-disable-next-line camelcase
                const position_zoom = this.settings.position_zoom ? this.settings.position_zoom : 1,
                        zoom_options = {
                            width: this.settings.zoom_width,
                            height: this.settings.zoom_height,
                        };

                const $section = $el.closest(".ecom-product-form--single");
                let zoomConfig = {
                    responsive: true,
                    scrollZoom: window.innerWidth > 768,
                    zoomType: window.innerWidth < 768 ? "inner" : "window",
                    zoomWindowPosition: position_zoom,
                    borderSize: 1,
                    zoomContainerAppendTo: $el.closest('.ecom-row'),
                    borderColour: "red"
                }
                let isTablet = window.matchMedia('only screen and (max-width: 768px)');



                const showThumbnails = this.settings.show_thumbnails
                    ? this.settings.show_thumbnails
                    : false;
                const layout = this.settings.layout ? this.settings.layout : "slider",
                      zoom_enable =  this.settings.enable_zoom ? this.settings.enable_zoom : false;
                if (layout === "slider") {
                    try {
                        var nav;
                        if (showThumbnails) {
                            const $selector = this.$el.querySelector(
                                ".ecom-product-single__media--thumbs"
                            );
                            let data = JSON.parse($selector.dataset.breakpoints);
                            Object.keys(data).forEach(s=>{
                                let __screen_name = data[s].__screen_name;
                                let key  = 'thumbnail_position';
                                data[s].direction = ['row','row-reverse'].includes(data[s][key]) ? 'vertical': 'horizontal';
                            })

                            nav = new window.EComSwiper($selector, {
                                freeMode: false,
                                centeredSlides: false,
                                loop: false,
                                centeredSlidesBounds: true,
                                slideToClickedSlide: true,
                                autoHeight: ['row','row-reverse'].includes(this.settings.thumbnail_position) ? true: false,
                                navigation: {
                                    nextEl: this.$el.querySelector(
                                        ".ecom-product-single__media--thumbs .ecom-swiper-button-next"
                                    ),
                                    prevEl: this.$el.querySelector(
                                        ".ecom-product-single__media--thumbs .ecom-swiper-button-prev"
                                    )
                                },
                                allowTouchMove: this.isLive,
                                watchSlidesProgress: true,
                                grabCursor: true,
                                centerInsufficientSlides: false,
                                breakpoints: data,
                                direction: ['row','row-reverse'].includes(this.settings.thumbnail_position) ? 'vertical': 'horizontal',
                                on:{
                                     slideChangeTransitionEnd: function () {
                                          //setTimeout(()=>window.dispatchEvent(new window.Event('resize')),500)
                                     },
                                     breakpoint: function() {
                                         setTimeout(()=>window.dispatchEvent(new window.Event('resize')),500)
                                     }
                                }

                            });
                            let itv = null;
                            $selector.querySelectorAll('img').forEach(function(img){
                                img.addEventListener('load', function(){
                                    clearTimeout(itv);
                                    itv = setTimeout(()=>window.dispatchEvent(new window.Event('resize')),500)
                                })
                            })


                        }
                        const $sliderElement = this.$el.querySelector(
                            ".ecom-product-single__media--featured"
                        );
                        var sliderBreakpoints = $sliderElement.dataset.breakpoints;
                        sliderBreakpoints = sliderBreakpoints ? JSON.parse(sliderBreakpoints): {'0': {slidesPerView: 1,spaceBetween: 20}};
                        let show_pagination = this.settings && this.settings.show_pagination ? true: false;
                        const slider = new window.EComSwiper($sliderElement, {
                            autoHeight: true,
                            lazy: true,
                            allowTouchMove: this.isLive,
                            pagination:show_pagination?  {
                                el: $sliderElement.querySelector('.ecom-swiper-pagination'),
                                dynamicBullets: true,
                                clickable: true
                            }: false,
                            navigation: {
                                nextEl: this.$el.querySelector(
                                    ".ecom-product-single__media--featured .ecom-swiper-button-next"
                                ),
                                prevEl: this.$el.querySelector(
                                    ".ecom-product-single__media--featured .ecom-swiper-button-prev"
                                )
                            },
                            thumbs: showThumbnails
                                ? {
                                      swiper: nav
                                  }
                                : undefined,
                            noSwiping: true,
                            longSwipes: true,
                            touchStartPreventDefault: true,
                            noSwipingSelector: "model-viewer",
                            noSwipingClass: "ecom-product-single__media--model",
                            grabCursor: true,
                            preloadImages: true,
                            breakpoints: sliderBreakpoints,
                            on: {
                                breakpoint: function() {
                                    setTimeout(()=>window.dispatchEvent(new window.Event('resize')),500)
                                },
                                slideChange: function (swiper) {
                                    if(swiper.activeIndex !== swiper.lastIndex)
                                    {
                                        swiper.lastIndex = swiper.activeIndex +'';
                                    }

                                    const $selector = $section.querySelector('[name="id"]');
                                    if ($selector) {
                                        let vid = null;
                                        if(!slider || !slider.slides[slider.activeIndex]) return;
                                        vid = slider.slides[slider.activeIndex]?.dataset?.variant_id;
                                        if (vid) {
                                            vid = vid + "";
                                            const id = $selector.value;
                                            if (!id || !vid.includes(id.toString())) {
                                                $selector.value = vid.split(",")[0];
                                                $selector.dispatchEvent(new Event("swatch"));
                                            }
                                        }
                                    }

                                    if(swiper.slides[swiper.activeIndex] && zoom_enable) {
                                        initZoom(swiper.slides[swiper.activeIndex]);
                                    }
                                },
                                slideChangeTransitionEnd: function (swiper) {
                                    window.dispatchEvent(new window.Event('resize'));

                                    swiper.slides.forEach(slide=>{
                                        if(!slide.classList.contains('ecom-swiper-slide-active'))
                                        {
                                            let player= slide.querySelector('iframe,video');
                                            if(player)
                                            {
                                                if(player.nodeName === 'IFRAME')
                                                {
                                                    player.src = player.src;
                                                    // player.contentWindow.postMessage('{"event":"command","func":"pauseVideo","args":""}', '*');
                                                }
                                                else
                                                {
                                                    player.pause();
                                                }
                                            }
                                        } else {
                                            let player= slide.querySelector('video');
                                            if(player) {
                                                if(player.hasAttribute('autoplay')) {
                                                    player.play();
                                                }
                                            }

                                        }
                                    })

                                    if(isLive)
                                   {
                                    //    let iframe = slider.slides[slider.activeIndex].querySelector('iframe');
                                    //    if(iframe)
                                    //    {

                                    //    }
                                        swiper.allowTouchMove = !swiper.slides[slider.activeIndex]?.classList?.contains('ecom-swiper-no-swiping');
                                   }
                                    // allowTouchMove

                                },
                                init: function (swiper) {
                                    if(swiper.slides[swiper.activeIndex] && zoom_enable) {
                                        initZoom(swiper.slides[swiper.activeIndex]);
                                    }
                                },
                            }
                        });
                        let itv = null;
                        $sliderElement.querySelectorAll('img').forEach(function(img){
                            img.addEventListener('load', function(){
                                clearTimeout(itv);
                                itv = setTimeout(()=>window.dispatchEvent(new window.Event('resize')),500)
                            })
                        })

                    } catch (e) {
                        console.info(e.message);
                    }
                }

                    $el.querySelectorAll(".ecom-product-single__media--play-control").forEach(function(el){
                        el.addEventListener('click', function(ev){
                            this.style.display = 'none';
                            this.parentNode.querySelector('video').play();
                        })
                    })


                /**
                 * Shopify AR
                 */
                if (!this.isLive) {
                    try {

                            _this.$el.querySelectorAll("model-viewer").forEach(function(el){
                                const html = element.outerHTML;
                                el.replaceWith(html);
                            })

                    } catch (e) {
                        console.info(e.message);
                    }
                }

                function setupModelViewerUI(errors) {
                    if (errors) {
                        return;
                    }
                    const $models = $el.querySelectorAll("model-viewer");
                    if ($models) {
                        $models.forEach(($model) => {
                            if ($model) {
                                try {
                                    // eslint-disable-next-line no-new
                                    new window.Shopify.ModelViewerUI($model);
                                } catch (e) {
                                    console.warn(e.message);
                                }
                            }
                        });
                    }
                    $el.querySelectorAll('button').forEach(function(el){
                        el.setAttribute('type','button');
                    })
                }

                function setupShopifyXr() {
                    if (!window.ShopifyXR) {
                        document.addEventListener("shopify_xr_initialized", function () {
                            setupShopifyXr();
                        });
                    } else {
                        try {
                            const $json_model = _this.$el.querySelector('[id^="Product-model-"]');
                            // eslint-disable-next-line camelcase
                            if ($json_model) {
                                window.ShopifyXR.addModels(JSON.parse($json_model.textContent));
                                $json_model.remove();
                            }
                        } catch (e) {
                            console.log(e.message);
                        }
                        window.ShopifyXR.setupXRElements();
                    }
                }
                if (
                    document.querySelector("model-viewer") &&
                    !document.getElementById("ModelViewerStyle")
                ) {
                    let link = document.createElement("link");
                    link.id = "ModelViewerStyle";
                    link.rel = "stylesheet";
                    link.href =
                        "https://cdn.shopify.com/shopifycloud/model-viewer-ui/assets/v1.0/model-viewer-ui.css";

                    link.media = 'print';
                    link.onload= function(){
                        this.media = 'all'
                    }
                    document.head.appendChild(link);

                }
                if(window.Shopify) {
                    window.Shopify.loadFeatures([
                        {
                            name: "shopify-xr",
                            version: "1.0",
                            onLoad: setupShopifyXr
                        },
                        {
                            name: "model-viewer-ui",
                            version: "1.0",
                            onLoad: setupModelViewerUI
                        }
                    ]);
                }

                /* Image Lightbox */
                if (this.settings.image_action && this.settings.image_action === "lightbox" && isLive) {
                    let arr =  this.$el.querySelectorAll('[ecom-modal]');
                    if(arr.length && window.EComModal) {
                        new window.EComModal(arr, {
                            gallery: true,
                            cssClass: ['ecom-container-lightbox-' + this.id]
                        });
                    }
                }

                /* Image Sticky */
                if(this.settings.position_sticky &&  window.innerWidth > 1024) {

                    if($el.parentElement) {
                        if(this.isLive) {
                            $el.style.height = "100%";
                        } else {
                            $el.parentElement.style.height = "100%";
                        }
                    }
                }

                /* Image Zoom */
                if(layout !== 'slider' && zoom_enable) {
                    let zoom = $el.querySelectorAll('.ecom-image-zoom');
                    if(zoom.length == 0) return;
                    initZoom(zoom);
                }
                function initZoom(el) {
                    if(!isLive || window.innerWidth < 768 ) return;
                    if(window.EcomImgZoom) {
                        if(el.length > 0) {
                            for (var i = 0, len = el.length; i < len; i++) {
                                new window.EcomImgZoom(el[i], zoom_options);
                            }
                        } else {
                            new window.EcomImgZoom(el, zoom_options);
                        }

                    }
                }

            };
        },

        liquids() {
            const classNameContainer = `ecom-product-single__media--image ecom-swiper-slide ecom-image-align${this.layout === "single" ? ' ecom-image-default' : ''}`;
            const tagName = "script";
            const slideControls =
                this.layout === "slider"
                    ? `
                <div class="ecom-swiper-pagination"></div>
                <div class="ecom-swiper-button-next ecom-swiper-controls" style="display: ${
                    this.sliderControls ? "flex" : "none"
                }">
                    ${
                        this.data.settings?.nextIcon?.value ??
                        `<svg xmlns="http://www.w3.org/2000/svg" class="w-6 h-6" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                            <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M9 5l7 7-7 7" />
                        </svg>`
                    }
                </div>
                <div class="ecom-swiper-button-prev ecom-swiper-controls" style="display: ${
                    this.sliderControls ? "flex" : "none"
                }">
                    ${
                        this.data?.settings?.prevIcon?.value ??
                        `<svg xmlns="http://www.w3.org/2000/svg" class="w-6 h-6" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                        <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M15 19l-7-7 7-7" />
                    </svg>`
                    }
                </div>
                `
                    : "";
            const slideControlsThumb =
                this.layout === "slider"
                    ? `
                <div class="ecom-swiper-button-next ecom-swiper-controls-thumb" style="display: ${
                    this.data.settings.sliderControlsThumb ? "flex" : "none"
                }">
                    ${
                        this.data.settings?.nextIconThumb?.value ??
                        `<svg xmlns="http://www.w3.org/2000/svg" class="w-6 h-6" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                            <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M9 5l7 7-7 7" />
                        </svg>`
                    }
                </div>
                <div class="ecom-swiper-button-prev ecom-swiper-controls-thumb" style="display: ${
                    this.data.settings.sliderControlsThumb ? "flex" : "none"
                }">
                    ${
                        this.data?.settings?.prevIconThumb?.value ??
                        `<svg xmlns="http://www.w3.org/2000/svg" class="w-6 h-6" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                        <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M15 19l-7-7 7-7" />
                    </svg>`
                    }
                </div>
                `
                    : "";

            const label = `
                    {%- liquid
                        if product.has_only_default_variant
                            assign target = product
                        else
                            assign target = product.selected_or_first_available_variant
                        endif
                    -%}

                    ${this.data.settings?.show_sale_sold_text || this.data.settings?.show_sale_badge ? `
                        <div class="ecom-product-single__media-label ecom-pa ecom-flex ecom-label-position__${this.data.settings?.label_position || 'topleft'}">
                        ${this.data.settings?.show_sale_sold_text ? `
                            {%- assign savings = product.compare_at_price | minus: product.price | times: 100.0 | divided_by: product.compare_at_price | round -%}
                            <span class="ecom-product-single__media-label-sale" data-text=" ${this.lang(this.sale_text, 'product_single_sale_text')}" data-sale="{{savings}}" {%- if product.compare_at_price == nil or product.compare_at_price <=  product.price -%}  style="display:none" {% endif %}>
                            ${this.lang(this.sale_text,'product_single_media_sale_text')}
                            </span>
                            <span class="ecom-product-single__media-label-sold-out" {% if target.available %} style="display:none" {% endif %}>
                                ${this.lang(this.sold_text, 'product_single_media_sold_text')}
                            </span>
                            ` :
                        ''}

                        ${this.data.settings?.show_sale_badge
                            ? `
                            {%- if product.compare_at_price != null and product.compare_at_price > product.price -%}
                                ${this.data.settings?.sale_badge_type =="amount"
                                        ? `{%- assign sale = product.compare_at_price | minus: product.price | money -%}`
                                        : `{%- assign sale = product.compare_at_price | minus: product.price | times: 100 | divided_by: product.compare_at_price | round -%}`
                                }
                                <span class="ecom-product-single__media-label--bage-sale" data-sale="${this.lang(this.data.settings?.bage_sale.replace('{{','[').replace('}}',']'))}" data-label-type="${this.data.settings?.sale_badge_type}">
                                    ${this.lang(this.data.settings?.bage_sale,"sale_badge",{ sale: "sale" })}
                                </span>
                            {%- endif -%}`
                            : ""
                        }

                        ${this.data.settings?.metafield_label
                            ? `
                            {%- capture meta_label-%}{{${this.data.settings.metafield_label}}}{%- endcapture -%}
                                {%- if meta_label != blank -%}
                                    <span class="ecom-product-single__media-label--metafield">
                                        {{meta_label}}
                                    </span>
                                {%- endif -%}
                                `
                            : ""
                        }

                        ${this.data.settings?.show_badges_tags && this.data.settings?.label_badge_tags
                            ? `
                                {% capture badge_tags %}${this.label_badge_tags}{% endcapture%}
                                {%- assign badge_tags = badge_tags | strip | split: ',' -%}
                                {% if badge_tags %}
                                    {% for badge in badge_tags %}
                                        {%- assign bad = badge | strip -%}
                                        {% if product.tags contains bad %}
                                            <span class="ecom-product-single__media-label--tags ecom-product-single__media-label--tags-{{ bad | handleize}}">
                                                {{ bad }}
                                            </span>
                                        {% endif %}
                                    {% endfor %}
                                {% endif %}

                                `
                            : ""
                        }
                        </div>
                        `
                        : ``
                    }
               `;

            const liquids = {
                product_grid: {
                    code: `
                    {% comment %}
                        Reactivity
                        *this.data.settings.image_action
                        *this.data.settings.enable_zoom
                    {% endcomment %}
                    <div class="ecom-swiper-wrapper ecom-product-single__media--images ecom-swiper-wrapper ecom-product-single__media--images-grid ecom-flex fl_wrap" >
                        {% for image in product.images %}
                            <div
                                class="${classNameContainer} ecom-product-single__media--image${this.data.settings?.grid_items ? ` ecom-col-lg-` + this.data.settings?.grid_items : ''}${this.data.settings?.grid_items__tablet ? ` ecom-col-md-` + this.data.settings?.grid_items__tablet : ''}${this.data.settings?.grid_items__mobile ? ` ecom-col-` + this.data.settings?.grid_items__mobile : ''}${this.enable_zoom ? ' ecom-image-zoom' :''}" ${this.outerZoom ? 'data-ecom-zoom-layout="outer"' : ''} data-index="{{forloop.index0}}"  data-variant_id="{{ image.variants | map:'id' | join: ','  }}"
                            >
                                ${this.enable_zoom ? `<a href="{{ image | img_url: "2048x2048"}}" class="ecom-img-zoom-a" data-ecom-role="zoom-target">` : ''}
                                    <img class="ecom-image-default" ${
                                        this.image_action === "lightbox"
                                            ? "    ecom-modal-source=\"{{image | img_url: 'master' }}\" ecom-modal=\"image\" "
                                            : ""
                                    } src="{{ image | img_url: "2048x2048"}}" alt="{{ image.alt | escape }}" {% unless forloop.first %} {% unless EComBuilderMode %} ${this.data.settings?.disable_lazyload ? '' : `loading="lazy"`} {% endunless %} {% endunless%} />
                                ${this.enable_zoom ? `</a>` : ''}
                            </div>
                        {% endfor %}
                        {%- for media in product.media -%}
                            {% case media.media_type %}
                                {% when 'image' %}
                                    {% continue %}
                                {% when 'external_video'%}
                                    <div class="${classNameContainer} ecom-product-single__media---external-video ecom-product-single__media--full" data-position="{{media.position}}" style="padding-top: {{ 1 | divided_by: media.aspect_ratio | times: 100 }}%;">
                                        {{ media | external_video_tag: image_size:'master' }}
                                    </div>
                                {% when 'video' %}
                                    <div data-stopdrag="true" class="${classNameContainer} ecom-product-single__media--video ecom-product-single__media--full ecom-swiper-no-swiping" data-position="{{media.position}}" {% comment %}style="padding-top: {{ 1 | divided_by: media.aspect_ratio | times: 100 }}%;"{% endcomment %}>
                                        {{ media | video_tag: image_size:'master', class: 'media-video', controls: true }}
                                        <button  class="ecom-product-single__media--play-control"
                                        type="button"
                                        >
                                            <span class="ecom-product-single__media--play-control-wrapper">
                                                <span class="visually-hidden">Play video</span>
                                                ${this.data.settings?.video_icon ? this.data.settings.video_icon.value : ''}
                                            </span>
                                        </button>
                                    </div>
                                {% when 'model' %}
                                    <div class="${classNameContainer} ecom-swiper-no-swiping  ecom-product-single__media--model ecom-product-single__media--full" data-stopdrag="true" data-position="{{media.position}}">
                                        <div class="ecom-product-single__media--model-wrapper" style="padding-top: 100%">
                                            {{ media | model_viewer_tag: image_size:'master', reveal: 'interaction', toggleable: true, data-model-id: media.id }}
                                        </div>
                                    </div>
                                {% else %}
                                    <div data-media-type="{{media.media_type}}" class="${classNameContainer} ecom-swiper-no-swiping ecom-product-single__media--full" data-position="{{media.position}}">
                                        <div class="ecom-product-single__media" style="padding-top: {{ 1 | divided_by: media.aspect_ratio | times: 100 }}%;">
                                            {{ media | media_tag: image_size:'master', class: 'ecom-product-single__media--item' }}
                                        </div>
                                    </div>
                                {% endcase %}
                            {%- endfor -%}
                        </div>

                        ${slideControls}
                    `,
                    preview: `
                            <div class="ecom-swiper-wrapper ecom-product-single__media-images">
                                <div class="${classNameContainer}">
                                    <img src="https://cdn2.shopify.com/s/files/1/0121/5945/1236/files/backpack.svg?7273" />
                                </div>
                                <div class="${classNameContainer}">
                                    <img loading="lazy" src="https://cdn2.shopify.com/s/files/1/0121/5945/1236/files/shoe.svg?7273" />
                                </div>
                                <div class="${classNameContainer}">
                                    <img loading="lazy" src="https://cdn2.shopify.com/s/files/1/0121/5945/1236/files/shoe.svg?7273" />
                                </div>
                            </div>
                    `
                },
                product_media: {
                    code: `
                    {% comment %}
                        Reactivity
                        *this.data.settings.image_action
                        *this.data.settings.enable_zoom
                    {% endcomment %}
                    ${label}
                    <div class="ecom-swiper-wrapper ecom-product-single__media--images ecom-swiper-wrapper ecom-product-single__media--images-layout__${this.layout}">
                        {% for image in product.images%}
                            <div class="${classNameContainer} ecom-product-single__media--image${this.enable_zoom ? ' ecom-image-zoom' :''}" ${this.outerZoom ? 'data-ecom-zoom-layout="outer"' : ''}  data-index="{{forloop.index0}}"  data-variant_id="{{ image.variants | map:'id' | join: ','  }}">
                                ${this.enable_zoom ? `<a href="{{ image | img_url: "2048x2048"}}" class="ecom-img-zoom-a" data-ecom-role="zoom-target">` : ''}
                                <img class="ecom-image-default${this.data.settings?.disable_lazyload ? '' : ` ecom-image-lazyload`}" ${
                                    this.image_action === "lightbox"
                                        ? "    ecom-modal-source=\"{{image | img_url: 'master' }}\" ecom-modal=\"image\" "
                                        : ""
                                } src="{{ image | img_url: "2048x2048"}}" alt="{{ image.alt | escape }}" {% unless forloop.first %} {% unless EComBuilderMode %} ${this.data.settings?.disable_lazyload ? '' : `loading="lazy"`} {% endunless %} {% endunless%} />
                                ${this.enable_zoom ? `</a>` : ''}
                            </div>
                        {% endfor %}
                        {%- for media in product.media -%}
                            {% case media.media_type %}
                                {% when 'image' %}
                                    {% continue %}
                                {% when 'external_video'%}
                                    <div class="${classNameContainer} ecom-product-single__media---external-video ecom-product-single__media--full" data-position="{{media.position}}" style="padding-top: {{ 1 | divided_by: media.aspect_ratio | times: 100 }}%;">
                                        {{ media | external_video_tag: image_size:'master' }}
                                    </div>
                                {% when 'video' %}
                                    <div data-stopdrag="true" class="${classNameContainer} ecom-product-single__media--video ecom-product-single__media--full ecom-swiper-no-swiping" data-position="{{media.position}}" {% comment %}style="padding-top: {{ 1 | divided_by: media.aspect_ratio | times: 100 }}%;"{% endcomment %}>
                                        {{ media | video_tag: image_size:'master', class: 'media-video', controls: ${this.data.settings?.video_control}, autoplay: ${this.data.settings?.video_auto_play}, mute: ${this.data.settings?.video_mute} }}
                                        ${this.data.settings?.video_auto_play ? ``
                                            : `
                                                <button  class="ecom-product-single__media--play-control" type="button">
                                                    <span class="ecom-product-single__media--play-control-wrapper">
                                                        <span class="visually-hidden">Play video</span>
                                                        ${this.data.settings?.video_icon ? this.data.settings.video_icon.value : ''}
                                                    </span>
                                                </button>
                                            `
                                        }
                                    </div>
                                {% when 'model' %}
                                    <div class="${classNameContainer} ecom-swiper-no-swiping  ecom-product-single__media--model ecom-product-single__media--full" data-stopdrag="true" data-position="{{media.position}}">
                                        <div class="ecom-product-single__media--model-wrapper" style="padding-top: 100%">
                                            {{ media | model_viewer_tag: image_size:'master', reveal: 'interaction', toggleable: true, data-model-id: media.id }}
                                        </div>
                                    </div>
                                {% else %}
                                    <div data-media-type="{{media.media_type}}" class="${classNameContainer} ecom-swiper-no-swiping ecom-product-single__media--full" data-position="{{media.position}}">
                                        <div class="ecom-product-single__media" style="padding-top: {{ 1 | divided_by: media.aspect_ratio | times: 100 }}%;">
                                            {{ media | media_tag: image_size:'master', class: 'ecom-product-single__media--item' }}
                                        </div>
                                    </div>
                                {% endcase %}
                            {%- endfor -%}
                        </div>

                        ${slideControls}
                    `,
                    preview: `
                            <div class="ecom-swiper-wrapper ecom-product-single__media-images">
                                <div class="${classNameContainer}">
                                    <img src="https://cdn2.shopify.com/s/files/1/0121/5945/1236/files/backpack.svg?7273" />
                                </div>
                                <div class="${classNameContainer}">
                                    <img loading="lazy" src="https://cdn2.shopify.com/s/files/1/0121/5945/1236/files/shoe.svg?7273" />
                                </div>
                                <div class="${classNameContainer}">
                                    <img loading="lazy" src="https://cdn2.shopify.com/s/files/1/0121/5945/1236/files/shoe.svg?7273" />
                                </div>
                            </div>
                    `
                },
                product_media_thumbs: {
                    code: `
                    {%- if product.media.size > 1 -%}
                    <div class="ecom-swiper-wrapper">
                        {% for image in product.images%}
                            <div class="ecom-product-single__media--thumbnail ecom-swiper-slide"  data-variant_id="{{ image.variants | map:'id' | join:','  }}">
                                <img
                                    class="ecom-product-thumbnail"
                                    src="{{ image | img_url: '${this.thumbnail_size}' ${this.thumbnail_crop !== "none" ? `, crop: '${this.thumbnail_crop}'` : ""} }}"
                                    alt="{{ image.alt  }}"
                                    ${this.data.settings?.disable_lazyload ? '' : `loading="lazy"`}
                                />
                            </div>
                        {% endfor %}
                        {%- for media in product.media -%}
                            {% if media.media_type != 'image' %}
                                <div class="ecom-product-single__media--thumbnail ecom-swiper-slide">
                                    <div class="ecom-product-single__media--thumbnail--icon">
                                        {% if media.media_type == 'model' %}
                                             ${this.data.settings?.thumbnail_model_icon ? this.data.settings.thumbnail_model_icon.value: ''}
                                        {% else %}
                                            ${this.data.settings?.thumbnail_video_icon ? this.data.settings.thumbnail_video_icon.value: ''}
                                        {% endif %}
                                    </div>
                                    <img src="{{ media.preview_image | img_url: '${this.thumbnail_size}' ${this.thumbnail_crop !== "none" ? `, crop: '${this.thumbnail_crop}'` : ""} }}"   alt="{{ media.alt }}" ${this.data.settings?.disable_lazyload ? '' : `loading="lazy"`}/>
                                </div>
                            {% endif %}
                        {%- endfor -%}
                        </div>
                        ${slideControlsThumb}
                        {%- endif -%}
                    `,
                    preview: `
                        <div class="ecom-swiper-wrapper">
                        <div class="ecom-product-single__media--thumbnail ecom-swiper-slide">
                            <img class="ecom-product-thumbnail" width="100"  src="https://cdn2.shopify.com/s/files/1/0121/5945/1236/files/backpack.svg?7273" />
                        </div>
                        <div class="ecom-product-single__media--thumbnail ecom-swiper-slide">
                            <img class="ecom-product-thumbnail"  width="100" src="https://cdn2.shopify.com/s/files/1/0121/5945/1236/files/shoe.svg?7273" />
                        </div>
                        <div class="ecom-product-single__media--thumbnail ecom-swiper-slide">
                            <img class="ecom-product-thumbnail" width="100"  src="https://cdn2.shopify.com/s/files/1/0121/5945/1236/files/shoe.svg?7273" />
                        </div>
                    </div>
                    `
                },
                thumb_count_class: {
                    code: `
                        {% if product.media.size == 1 %}
                            ecom-dont-has-many-images
                         {% endif %}
                    `,
                    preview: ""
                },
                product_model: {
                    code: `
                        <${tagName} type="application/json" id="Product-model-{{ product.id }}">
                            {{ product.media | where: 'media_type', 'model' | json }}
                        </${tagName}>
                    `,
                    preview: ""
                },
                featured_image: {
                    code: `
                            <div class="${classNameContainer} ecom-product-single__media--image${this.enable_zoom ? ' ecom-image-zoom' :''}" ${this.outerZoom ? 'data-ecom-zoom-layout="outer"' : ''}" >
                                ${this.enable_zoom ? `<a href="{{ product.featured_image | img_url: "2048x2048"}}" class="ecom-img-zoom-a" data-ecom-role="zoom-target">` : ''}
                                    <img
                                        ${
                                            this.image_action === "lightbox"
                                                ? " {% if EComBuilderMode != true %}   ecom-modal-source=\"{{product.featured_image | img_url: 'master' }}\" ecom-modal=\"image\" {% endif %}"
                                                : ""
                                        }
                                        src="{{ product.featured_image | img_url: "2048x2048"}}"
                                        alt="{{ product.featured_image.alt | escape }}"
                                        ${this.data.settings?.disable_lazyload ? '' : `loading="lazy"`}
                                    />
                                ${this.enable_zoom ? `</a>` : ''}
                            </div>
                        `,
                    preview: `
                        <div class="${classNameContainer}">
                                <img src="https://cdn2.shopify.com/s/files/1/0121/5945/1236/files/backpack.svg?7273" />
                        </div>
                        `
                }
            };
            if (this.layout === "slider" && this.show_thumbnails) {
                return {
                    product_media: liquids.product_media,
                    product_media_thumbs: liquids.product_media_thumbs,
                    product_model: liquids.product_model,
                    thumb_count_class : liquids.thumb_count_class
                };
            }
            if (this.layout === "single") {
                return {
                    featured_image: liquids.featured_image
                };
            }
            return {
                product_media: liquids.product_media,
                product_model: liquids.product_model,
                product_grid: liquids.product_grid
            };
        },

        css() {
            return `
                .ecom-image-lazyload {
                    min-height: 150px;
                }
                .ecom-product-single__media--thumbs:empty {
                    display: none !important;
                }
                .ecom-product-single__media {
                    display: block;
                    position: relative;
                    width: 100%;
                }
                .ecom-product-single__media--grid .ecom-product-single__media--images img {
                    max-width: 100%;
                }

                .ecom-product-single__media--grid .ecom-product-single__media--images {
                    display: flex;
                    flex-wrap: wrap;
                    margin-bottom: 2rem;
                    padding: 0;
                    gap: 10px;
                    list-style: none;
                }

                .ecom-product-single__media--grid .ecom-product-single__media--image {
                    width: calc(50% - 1rem / 2);
                    max-width: 100%;
                    flex-grow: 1;
                }
                /** Set the media image and the first image 100% width **/

                .ecom-product-single__media--grid .ecom-product-single__media--image:first-child {
                    width: 100%;
                }

                .ecom-product-single__media--grid .ecom-product-single__media--image.ecom-product-single__media--full {
                    width: 100%;
                }

                .ecom-product-single__media-label > span {
                    height: fit-content;
                }

                .ecom-swiper-controls::after {
                    display: none
                }

                .ecom-product-single__media--grid .ecom-swiper-controls {
                    display: none;
                }

                .ecom-product-single__media--grid .ecom-product-single__media--images img,
                .ecom-product-single__media--video video {
                    max-width: 100%;
                }

                .ecom-product-single__media--video video {
                    width: 100%;
                }

                .ecom-product-single__media--grid .ecom-product-single__media--image:first-child {
                    width: 100%;
                }

                .ecom-product-single__media--grid .ecom-product-single__media--image.ecom-product-single__media--full {
                    width: 100%;
                }

                .ecom-product-single__media---external-video {
                    position: relative;
                }

                .ecom-product-single__media--model {
                    position: relative;
                }
                .ecom-modal .ecom-swiper-wrapper .ecom-swiper-slide img {
                    margin: auto !important;
                }
                .ecom-product-single__media---external-video iframe,
                .ecom-product-single__media--model-wrapper model-viewer {
                    display: block;
                    max-width: 100%;
                    position: absolute;
                    top: 0;
                    left: 0;
                    height: 100%;
                    width: 100%;
                }

                .ecom-product-single__media .shopify-model-viewer-ui.shopify-model-viewer-ui--desktop {
                    display: block;
                    max-width: 100%;
                    position: absolute;
                    top: 0;
                    left: 0;
                    height: 100%;
                    width: 100%;
                }
                .ecom-product-single__media--image {
                    min-height: 100px;
                }
                .ecom-swiper-wrapper {
                    align-items: stretch;
                }

                .ecom-swiper-wrapper .ecom-swiper-slide {
                    text-align: center;
                }

                .ecom-swiper-wrapper .ecom-swiper-slide img {
                    height: 100%;
                    object-fit: contain;
                }

                .ecom-product-single__media--thumbnail {
                    border: 2px solid transparent;
                    cursor: pointer;
                    display: flex;
                    justify-content: center;
                }

                .ecom-product-single__media--thumbnail img {
                    width: 100%;
                    max-width: 100%;
                    height: auto;
                    border: 0;
                    vertical-align: middle;
                    position: relative;
                    z-index: 1;
                }

                .ecom-swiper-controls svg {
                    height: 100%;
                    width: 100%;
                    color: inherit;
                }

                .ecom-product-single__media-wrapper .ecom-swiper-controls {
                    cursor: pointer;
                    width: auto;
                    height: auto;
                    transition: .2s ease-in-out;
                }
                .ecom-swiper-controls-thumb svg {
                    height: 100%;
                    width: 100%;
                    color: inherit;
                }
                .ecom-product-single__media--thumbs{
                    position:relative
                }
                .ecom-swiper-button-thumb-prev{
                    left:0
                }
                .ecom-swiper-controls-thumb > svg{
                    width:24px;
                    height:24px;
                }

                .ecom-swiper-controls-thumb {
                    z-index:10;
                    display:flex;
                    cursor: pointer;
                    width:auto;
                    height:auto;
                    transition: .2s ease-in-out;
                    text-align: center;
                }
                .ecom-swiper-controls-thumb::after{
                    display:none
                }

                .ecom-swiper-controls:after {
                    margin-left: -3px;
                }


                .ecom-product-single__media--thumbnail {
                    border-radius: 6px;
                }

                .ecom-product-single__media--thumbnail:hover {
                    z-index: 10;
                }

                button.ecom-product-single__media--play-control {
                    display: block;
                    max-width: 100%;
                    position: absolute;
                    top: 0;
                    left: 0;
                    height: 100%;
                    width: 100%;
                    border: none;
                    cursor: pointer;
                    margin: 0;
                    padding: 0;
                    background-color:rgb(238 238 238 / 40%);
                }

                .ecom-product-single__media--play-control-wrapper {
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    position: absolute;
                    left: 50%;
                    top: 50%;
                    transform: translate(-50%, -50%) scale(1);
                    z-index: 1;
                }
                .ecom-product-single__media--thumbnail {
                    position: relative;
                }

                .ecom-product-single__media--thumbnail--icon {
                    position: absolute;
                    top: 50%;
                    left: 50%;
                    transform: translate(-50%, -50%);
                    z-index: 3;
                    display: flex;
                }

                .ecom-product-single__media--thumbnail--icon svg {
                    display: flex;
                }
                .ecom-product-single__media--horizontal .ecom-swiper-controls-thumb,
                .ecom-product-single__media-tablet--horizontal .ecom-swiper-controls-thumb,
                ecom-product-single__media-mobile--horizontal .ecom-swiper-controls-thumb{
                    transform:translateY(50%);
                }
                .ecom-dont-has-many-images .ecom-product-single__media--thumbs {
                    display: none;
                }

                .ecom-image-align {
                    display: flex;
                }

                .ecom-product-single__media .ecom-product-single__media-container {
                    display: flex;
                    flex-direction: column;
                    gap: 10px
                }

                .ecom-swiper-container {
                    width: 100%;
                }
                .ecom-product-single__media--thumbnail > * {
                    transition:inherit;
                }
                .ecom-product-single__media--images-grid .ecom-swiper-slide {
                    margin-bottom: 15px;
                }

                /* Image Zoom */
                .ecom-image-zoom {
                    position: relative;
                    display: inline-block;
                    overflow: hidden;
                }

                .ecom-image-zoom > a {
                    cursor: zoom-in;
                }

                .ecom-image-zoom.is-error > a {
                    cursor: not-allowed;
                }

                .ecom-image-zoom.is-loading > a {
                    cursor: progress;
                }

                .ecom-image-zoom > a > img {
                    display: block;
                }

                .ecom-image-zoom-notice {
                    position: absolute;
                    top: 50%;
                    left: 50%;
                    z-index: 150;
                    width: 10em;
                    margin: -1em 0 0 -5em;
                    line-height: 2em;
                    text-align: center;
                    background: #FFF;
                    box-shadow: 0 0 10px #888;
                }

                .ecom-image-zoom-flyout {
                    position:absolute;
                    top: 0;
                    left: 0;
                    width: 100%;
                    height: 100%;
                    z-index: 200;
                    overflow: hidden;
                    background: #FFF;
                    cursor: crosshair;
                }
                .ecom-image-zoom-window.ecom-image-zoom-flyout {
                    opacity: 0;
                    box-shadow: 0 1px 5px rgba(127,127,127,0.02), 0 5px 18px rgba(127,127,127,0.2);
                }
                .ecom-image-zoom-flyout.ecom-open {
                    animation: fadeZoomIn 200ms cubic-bezier(0.4, 0, 0.2, 1) forwards;
                    -webkit-animation: fadeZoomIn 200ms cubic-bezier(0.4, 0, 0.2, 1) forwards;
                }

                .ecom-image-zoom-flyout.ecom-close {
                    animation: fadeZoomOut 150ms cubic-bezier(0, 0, 0.2, 1)
                }

                .ecom-image-zoom-flyout img {
                    width: auto;
                    max-width: none !important;
                    height: auto !important;
                    object-fit: unset important;

                }
                .ecom-product-single__media .ecom-image-align {
                    justify-content: center;
                }
                .ecom-product-single__media-label {
                    z-index: 99;
                    pointer-events: none;
                }
                .ecom-label-position__topleft {
                    top: 20px;
                    left: 20px;
                }
                .ecom-label-position__topright {
                    top: 20px;
                    right: 20px;
                }
                .ecom-label-position__bottomleft {
                    bottom: 20px;
                    left: 20px;
                }
                .ecom-label-position__bottomright {
                    bottom: 20px;
                    right: 20px;
                }
                @media screen and (min-width: 1025px) {
                    .ecom-product-single__media.ecom-position-sticky {
                        position: sticky;
                        top: 0
                    }
                    .ecom-product-single__media .ecom-product-single__media--vertical.ecom-product-single__media--slider {
                        display: flex;
                        flex-flow: row-reverse;
                    }
                    .ecom-product-single__media .ecom-product-single__media--vertical .ecom-product-single__media--thumbs {
                        min-width: 100px;
                    }
                    .ecom-product-single__media--vertical .ecom-product-single__media--featured {
                        width: auto;
                    }
                    .ecom-product-single__media--vertical .ecom-product-single__media--thumbs .ecom-swiper-button-next{
                        right:auto;
                        left:50%;
                        transform:translateX(-50%);
                        top:auto;
                        bottom:10px;
                    }
                    .ecom-product-single__media--vertical .ecom-product-single__media--thumbs .ecom-swiper-controls-thumb svg{
                        transform: rotate(90deg);
                    }
                    .ecom-product-single__media--vertical .ecom-product-single__media--thumbs .ecom-swiper-button-prev{
                        right:auto;
                        left:50%;
                        transform:translateX(-50%);
                        top:10px;
                        bottom:auto;
                    }
                    .ecom-product-single__media .ecom-product-single__media--horizontal .ecom-product-single__media--thumbs {
                        min-height: 100px;
                        height: auto;
                    }
                }
                @media (min-width: 768px) and (max-width: 1024px) {
                    .ecom-product-single__media--thumbs.ecom-swiper-tablet-vertical {
                        min-width: 100px;
                        width: auto;
                    }
                    .ecom-product-single__media-tablet--vertical .ecom-product-single__media--thumbs .ecom-swiper-button-next{
                        right:auto;
                        left:50%;
                        transform:translateX(-50%);
                        top:auto;
                        bottom:10px;
                    }
                    .ecom-product-single__media-tablet--vertical .ecom-product-single__media--thumbs .ecom-swiper-controls-thumb svg{
                        transform: rotate(90deg);
                    }
                    .ecom-product-single__media-tablet--vertical .ecom-product-single__media--thumbs .ecom-swiper-button-prev{
                        right:auto;
                        left:50%;
                        transform:translateX(-50%);
                        top:10px;
                        bottom:auto;
                    }
                }
                @media (max-width: 767px) {
                    .ecom-product-single__media--vertical-mobile .ecom-product-single__media--featured {
                        width: auto;
                    }
                    .ecom-product-single__media--thumbs.ecom-swiper-mobile-vertical {
                        min-width: 100px;
                        width: auto;
                    }
                    .ecom-product-single__media-mobile--vertical .ecom-product-single__media--thumbs .ecom-swiper-button-next{
                        right:auto;
                        left:50%;
                        transform:translateX(-50%);
                        top:auto;
                        bottom:10px;
                    }
                    .ecom-product-single__media-mobile--vertical .ecom-product-single__media--thumbs .ecom-swiper-controls-thumb svg{
                        transform: rotate(90deg);
                    }
                    .ecom-product-single__media-mobile--vertical .ecom-product-single__media--thumbs .ecom-swiper-button-prev{
                        right:auto;
                        left:50%;
                        transform:translateX(-50%);
                        top:10px;
                        bottom:auto;
                    }
                }
                @keyframes fadeZoomIn {
                0% {
                    transform: scale(0.8);
                    opacity: 0; }

                100% {
                    transform: scale(1);
                    opacity: 1; } }

                @keyframes fadeZoomOut {
                0% {
                    opacity: 1; }

                100% {
                    opacity: 0; } }

            `;
        },

        layout() {
            return this.data?.settings?.layout ? this.data.settings.layout : "slider";
        },
        image_action() {
            return this.data?.settings?.image_action ?? "nothing";
        },
        enable_zoom() {
            return this.data &&
                this.data.settings &&
                "enable_zoom" in this.data.settings
                ? this.data.settings.enable_zoom
                : false;
        },
        outerZoom() {
            return this.data &&
                this.data.settings &&
                "enable_zoom" in this.data.settings &&
                this.data.settings.enable_zoom
                ? this.data.settings?.zoom_type == 'outer'
                : false;
        },
        show_thumbnails() {
            return this.data?.settings?.show_thumbnails ?? false;
        },

        sliderControls() {
            return this.data && this.data.settings && this.data.settings.sliderControls
        },

        requestShopifyType() {
            return {
                shopify_type: "product"
            };
        },

        thumbnail_size() {
            return (
                (this.data?.settings?.thumbnail_width ?? "") +
                "x" +
                (this.data?.settings?.thumbnail_height ?? "")
            );
        },
        thumbnail_crop() {
            return this.data?.settings?.thumbnail_crop ?? "none";
        },

        default() {
            return   {
                "settings": {
                    "layout": "slider",
                    "show_pagination": false,
                    "show_thumbnails": true,
                    "thumbnail_position": "column",
                    "slidesPerView": 4,
                    "thumbnail_position__tablet": "column",
                    "slidesPerView__tablet": 4,
                    "slidesPerView__mobile": 4,
                    "thumbnail_position__mobile": "column",
                    "sliderControls": true,
                    "zoom_height": "500px",
                    "zoom_width": "500px",
                    "sale_text": "Sale",
                    "sold_text": "Sold out",
                    "bage_sale": "-{{sale}}%",
                    "label_badge_tags": "hot, new"
                },
